export const Block3 = () => {

    let windowWidth = useWindowWidth()
    let isDesk = windowWidth > 1365

    let content = []

    for (let i = 0; i < 15; i++) {
        content.push(
            <div className={styles.cardItem}>
                <SVGcontainer svgIcon={AlvaIcon} withHover={true} specialColor='red' />
            </div>
        )
    }

    return (
        <section className={styles.wrapper}>
            <div className={styles.mainContainer}>
                <TitleAndDescription title='Our Partners' />

                <SliderBlank perView={isDesk ? 6 : 1.5} spaceBetween={isDesk ? 80 : 20} withPagination={false} items={content} />
            </div>
        </section>
    )
}

